body {
  background: black;
  color: rgb(218, 232, 210);
  font-family: 'Oswald', regular;
  border-radius: 3px;
}
.img-center {
    display: block;
    margin: 0 auto;
}

.nav-item {
  font-size: 1.25rem;
}

h1 {
  font-size: 2rem;
}

.nav-item:hover, .nav-item:active {
  background: rgb(128, 128, 128);
}


/* unvisited link */

a:link {
  color: #2c3e50;
}


/* visited link */

a:visited {
  color: white;
}


/* mouse over link */

a:hover {
  color: white;
}


/* selected link */

a:active {
  color: white;
}

h1 {}


/*
.menu {
  position:fixed;
  left:0;
  right:0;
  z-index:2;
} */

.carousel-caption {
  font-family: 'Cardo', regular;
}

.hard-to-see {
  /* text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white; */
}

.carousel-caption p {
  color: black;
}

.carousel-caption h1 {
  color: black;
}

#about {
}

#portfolio {
}

img {
  max-width: 100%
}

.portfolio.col {
    background-color: #16a085;
    margin: 3px 3px 3px 3px;

}

#contact {
}

.portfolio-image {
  width: 80%;
  height: 40%
}
.hidden {
  opacity: .0
}

.visible {
  opacity: 1
}

div.port-item {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

div.port-item:hover, div.port-item:active {
  background-color: #06EAC9;
}
.contact-icon {
  width: 30%;
  height: 30%;
}

@-webkit-keyframes arrowAnimation {
  0% { left:50%; }
  45% { left:100%; }
  55% { left:100%;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
   }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

}
@keyframes arrowAnimation {
  50% {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
   }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

}
.hack-arrow {
  fill: #16a085;
  -webkit-transition: 10s;
  -o-transition: 10s;
  transition: 10s;
  -webkit-animation: arrowAnimation Infinite;
          animation: arrowAnimation Infinite;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}

#navbarCollapse {
  opacity: .7;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.carousel-caption {
  background: #7da6a2;
  opacity: .75;
}

.carousel-caption:hover, .carousel-caption:active, .carousel-caption:focus {
  background: #7da6a2;
  opacity: .95;
}

@media screen and (max-width: 375px) {
  .carousel-caption  {
  opacity: 0.95
}
}

.hard-to-see, .carousel-caption h1, .carousel-caption p {
  opacity: 1;
}

.container-item {
  background-color: #16a085;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
  border-radius: 3px;
  color: black;
  /* margin: 20px 20px 20px 20px; */

}

.acknowledgments {
  font-size: 8px;

}

.container-item:hover, .container-item:active {
  background-color: #06EAC9;
}

.read-me {
  font-family: 'Cardo', regular;
}

.minor-about-me {
  font-size: 14px;
}

.portfolio img {
  max-height: 300px;
  height: auto;
}

#navbarCollapse:hover {
  opacity: 1;
}

.first-nav-item .nav-item {
  color: #16a085;
}

.col {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -webkit-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
}

img {
    vertical-align: middle;
    /* max-height: 250px;
    max-width: 160px; */
}



